<template>
  <nav class="nav">
    <a href="#" class="logo">
      <img src="@/assets/img/logo.svg" alt="" />
      <span class="logo__span">vue weather</span>
    </a>
    <div class="nav__search">
      <img src="@/assets/img/drop.svg" @click="darkMode" alt="" />
      <input type="text" placeholder="Выбрать город" class="nav__search-input" v-model="city"
        @keydown.enter="getWeather" />
    </div>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      city: "",
    };
  },
  methods: {
    getWeather() {
      this.$store.dispatch("getWeather", this.city);
    },
    darkMode() {
      let value = document.body.style.getPropertyValue('--bg')
      if (value == 'white') {
        document.body.style.setProperty('--bg', 'rgb(27,27,27)')
        document.body.style.setProperty('--text', 'white')
      } else {
        document.body.style.setProperty('--bg', 'white')
        document.body.style.setProperty('--text', 'black')
      }
    }
  },
  created() {
    document.body.style.setProperty('--bg', 'white')
    document.body.style.setProperty('--text', 'black')
  }
};
</script>
